
.carousel{position:relative;overflow:hidden;box-shadow:0 2px 4px 0 rgba(0,0,0,0.2),0 25px 50px 0 rgba(0,0,0,0.1)
}
.carousel-inner{transition:all 0.3s
}
.carousel.pagination{padding:10px 10px 15px 10px;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-line-pack:center;align-content:center
}
.carousel.pagination .slide-pagination{width:10px;height:10px;background:black;margin-right:10px;transition:all 0.3s
}
.carousel.pagination .slide-pagination:last-of-type{margin-right:0
}
.carousel.pagination .slide-pagination:hover{cursor:pointer;opacity:0.5
}
.carousel.pagination .slide-pagination.active{background:transparent;border:1px solid black
}
.carousel-navigation{position:absolute;width:80px;height:80px;font-size:80px;background:black;top:calc(50% - 40px);z-index:100;color:white;transition:all 0.3s;opacity:0.5
}
.carousel-navigation.prev{left:0
}
.carousel-navigation.next{right:0
}
.carousel-navigation:hover{cursor:pointer;opacity:0.9;width:100px
}



