
.slide-fade-enter-active{transition:all .3s ease
}
.slide-fade-enter{opacity:0;transform:scale(1.2)
}
.tabs-container ul{margin-bottom:0;text-align:left;padding-left:0
}
.tabs-container ul li{transition:all 0.3s;display:inline-block;padding:20px 20px 10px 20px;border-top-left-radius:5px;border-top-right-radius:5px
}
.tabs-container ul li.active{background:rgba(0,0,0,0.16)
}
.tabs-container ul li:hover{cursor:pointer;background:rgba(0,0,0,0.06)
}
.tabs-container ul li:last-of-type{margin-right:0
}



