
.project-detail{display:-ms-flexbox;display:flex;-ms-flex-direction:row;flex-direction:row;-ms-flex-wrap:wrap;flex-wrap:wrap
}
@media screen and (max-width: 720px){
.project-detail{-ms-flex-direction:column;flex-direction:column
}
}
.project-detail .content{-ms-flex:3;flex:3;padding-right:40px
}
@media screen and (max-width: 720px){
.project-detail .content{padding-right:0
}
}
.project-detail .metas{-ms-flex:1;flex:1
}
