
.feature{margin-top:20px;text-align:center;margin-bottom:30px
}
.feature .icon-container{position:relative;display:inline-block;padding:20px;z-index:1
}
.feature .icon-container:before{transition:all 0.3s ease-in;content:'';position:absolute;left:0;top:0;width:100%;height:100%;background:rgba(0,0,0,0.08);border-radius:50%;z-index:1
}
.feature .icon-container:after{transition:all 0.3s ease-in;content:'';position:absolute;left:-10%;top:-10%;width:120%;height:120%;background:rgba(0,0,0,0.05);border-radius:50%
}
.feature .icon-container i.icon{font-size:2.5rem;line-height:2.5rem;opacity:0.8
}
.feature .icon-container:hover{cursor:pointer
}
.feature .icon-container:hover:after{transform:scale(0)
}
.feature .icon-container:hover:before{transform:scale(1.1)
}
.feature p{text-align:left
}
.feature h4{margin-top:1rem;padding-bottom:1rem;border-bottom:3px solid #005f6b
}
@media screen and (max-width: 720px){
.feature h4{width:60%;margin:1.2rem auto
}
}



