*{box-sizing:border-box
}
html{font-size:100%
}
.container p{text-align:justify
}
h3{font-family:'Geomanist', 'Lato', 'Roboto', sans-serif;font-size:1.6rem;text-transform:uppercase
}
body{font:14px 'Lato', Helvetica, Arial, sans-serif;line-height:1.4em;background:#343838;color:whitesmoke;-webkit-font-smoothing:antialiased;-moz-font-smoothing:antialiased;font-smoothing:antialiased;font-weight:300
}
body.lightbox-opened{overflow:hidden
}
#app{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:whitesmoke
}
p.hero{padding:10px;background:#005f6b;text-align:center;font-size:1.2rem;box-shadow:0 4px 20px 0px rgba(0,0,0,0.13)
}
hr{margin:50px 0;border:none;border-bottom:5px solid #005f6b;border-top:none
}
nav.aside{z-index:100;transition:all .3s ease-in;position:fixed;display:-ms-flexbox;display:flex;width:300px;background:#2c2f2f;box-shadow:inset -16px 0px 25px -20px rgba(0,0,0,0.2);height:100%;color:whitesmoke;-ms-flex-direction:column;flex-direction:column;text-align:center;font-size:0.95rem
}
nav.aside .item.brand{padding:40px
}
nav.aside .item.brand h1>a{color:inherit;display:inline-block;font-size:1.5rem;transition:all .3s
}
nav.aside .item.brand h1>a:before{transition:all .3s;content:'[';margin-right:10px;color:#005f6b;display:inline-block;transform:translateX(-10px) scale(0.8) rotate(-25deg);opacity:0
}
nav.aside .item.brand h1>a:after{transition:all .3s;content:']';display:inline-block;margin-left:10px;color:#005f6b;opacity:0;transform:translateX(10px) scale(0.8) rotate(25deg)
}
nav.aside .item.brand h1>a:hover:before{opacity:1;transform:translateX(0) scale(1) rotate(0deg)
}
nav.aside .item.brand h1>a:hover:after{opacity:1;transform:translateX(0) scale(1) rotate(0deg)
}
nav.aside .item.brand h1>a:hover{cursor:pointer;color:#00bad1;transform:scale(1.1)
}
nav.aside ul{list-style:none;padding:0;margin:0 0 20px 0
}
nav.aside ul li{transition:all .3s;display:block;border-bottom:2px solid rgba(245,245,245,0.03)
}
nav.aside ul li:hover{cursor:pointer;background:#343838
}
nav.aside ul li:first-of-type{border-top:2px solid rgba(245,245,245,0.03)
}
nav.aside ul li a{transition:all .3s;display:inline-block;width:100%;padding:20px;color:#008c9e
}
nav.aside ul li a:before{transition:all .3s;display:inline-block;content:'>';margin-right:10px;color:#005f6b;opacity:0;transform:translateX(-50px)
}
nav.aside ul li a:hover{color:#00b4cc
}
nav.aside ul li a:hover:before{opacity:1;transform:translateX(0)
}
nav.aside .theme-chooser select{padding:5px;color:black
}
nav.aside.closed{transform:translateX(-300px)
}
nav.aside.opened{transform:translateX(0)
}
nav.aside footer{padding:15px;-ms-flex-pack:center;justify-content:center;margin-top:80px
}
nav.aside footer a{margin-right:10px
}
nav.aside footer a:last-of-type{margin-right:0
}
nav.aside footer i{transition:opacity .3s;opacity:0.3
}
nav.aside footer i:hover{cursor:pointer;opacity:1
}
nav.aside footer .copyright{margin-top:10px;opacity:0.5;font-size:0.8rem
}
.router-link-active:not(a[href='/']){background:yellow
}
a>i[class*="icon"]{color:white
}
