.theme-selector .light-theme{border:2px solid #008c9e;background:whitesmoke
}
.theme-selector .dark-theme,.theme-selector .light-theme{transition:opacity .3s;width:32px;height:32px;position:relative;opacity:0.2
}
.theme-selector .dark-theme.active,.theme-selector .light-theme.active{opacity:1
}
.theme-selector .dark-theme:hover,.theme-selector .light-theme:hover{cursor:pointer
}
.theme-selector .dark-theme:hover:not(.active),.theme-selector .light-theme:hover:not(.active){opacity:0.5
}
a.lightbox-image{display:inline-block;transition:all .3s;margin:5px
}
a.lightbox-image:hover{box-shadow:0 2px 4px 0 rgba(0,0,0,0.3),0 25px 50px 0 rgba(0,0,0,0.2);cursor:pointer
}
a.lightbox-image img{vertical-align:bottom;max-width:100%
}
.mobile-only{display:none !important
}
.fade-enter-active,.fade-leave-active{transform-origin:top right;transition:opacity .5s, filter 0.7s
}
.fade-enter,.fade-leave-active{transform-origin:left top;opacity:0;filter:blur(5px)
}
.fadeFromLeft-enter-active,.fadeFromLeft-leave-active{transition:opacity .3s, filter 0.3s
}
.fadeFromLeft-enter,.fadeFromLeft-leave-active{opacity:0;filter:blur(5px)
}
.fade-move{transition:transform 0.3s
}
.slide-heading-enter-active,.slide-heading-leave-active{transition:opacity .5s, transform 0.8s
}
.slide-heading-enter,.slide-heading-leave-active{opacity:0;transform:translateX(10px)
}
@keyframes logo{
0%{transform:scale(1) rotate(0deg)
}
25%{transform:scale(1.05) rotate(-5deg)
}
50%{transform:scale(1.05) rotate(5deg)
}
100%{transform:scale(1) rotate(0deg)
}
}
@media screen and (max-width: 720px){
.lightbox .lightbox-image{height:calc(100vh - 120px) !important;top:0 !important
}
nav.aside{transform:translateX(-300px);overflow-y:scroll;padding-bottom:30px
}
.view-wrapper{padding-left:0
}
.container{padding:20px;width:100vw
}
.container.offset{margin-top:297px
}
.current-page.is-project.fixed ~ .view.offset{margin-top:242px
}
img{max-width:100%
}
a.project.image{width:calc(100% - 40px)
}
.current-page{padding:20px;width:100%;position:fixed;top:0;left:0
}
.current-page span.slash{font-size:1.8rem;margin-right:0;margin-left:4px
}
.current-page h2{font-size:1.5rem
}
header.current-page.is-project.fixed{transition:none;height:58px;position:fixed
}
header.current-page.is-project.fixed i{position:relative;bottom:12px
}
.lightbox .lightbox-image h2{font-size:1rem
}
.lightbox .lightbox-image .description{font-size:0.9rem
}
.projects-list .project{box-shadow:0 2px 4px 0 rgba(0,0,0,0.1),0 25px 50px 0 rgba(0,0,0,0.1),inset 0 0 200px 80px rgba(0,0,0,0.8) !important;width:100% !important;max-width:calc(100% - 20px)
}
.projects-container .categories span:nth-of-type(even){margin-right:0
}
.projects-container .categories div span:first-of-type{margin-right:7px
}
.project-title,.project-description,.project-categories{opacity:1 !important;visibility:visible !important;transform:scale(1) !important;filter:blur(0) !important
}
p{line-height:1.90rem
}
.view{margin-top:58px
}
.is-project ~ .view{margin-top:0
}
h2{font-size:1.4rem
}
h3{font-size:1.3rem
}
.mobile-only{display:block !important
}
i.mobile-only{display:inline-block !important
}
html{font-size:85%
}
.projects-container .categories span[class~="button"]{display:inline-block;width:45%
}
header.current-page{transition:none;height:58px;padding:10px;line-height:40px;z-index:9999
}
header.current-page.is-project i{position:relative;bottom:10px
}
header.current-page code{max-height:26px;overflow:hidden;font-size:1.2rem
}
}
@media screen and (max-width: 1120px){
.projects-container .categories{-ms-flex-direction:column !important;flex-direction:column !important
}
}



