
.projects-list .project{display:-ms-inline-flexbox;display:inline-flex;-ms-flex-direction:column;flex-direction:column;-ms-flex-pack:justify;justify-content:space-between;-ms-flex-align:center;align-items:center;width:350px;height:220px;margin:10px;transition:all .3s;padding:20px;color:whitesmoke
}
.projects-list .project .project-description{transition:all .3s;filter:blur(5px);opacity:0;visibility:hidden;transform:scale(0.5)
}
.projects-list .project .project-categories{transition:all .3s;filter:blur(5px);opacity:0;visibility:hidden;transform:scale(0.5)
}
.projects-list .project .project-categories span{display:inline-block;margin-right:15px
}
.projects-list .project .project-categories span:last-of-type{margin-right:0
}
.projects-list .project .project-title{transition:all .3s;opacity:0;visibility:hidden;filter:blur(5px);color:whitesmoke;font-size:1.5rem;font-family:'Geomanist', 'Lato', 'Roboto', sans-serif;text-transform:uppercase;transform:scale(0.5);text-shadow:0 0 20px rgba(0,0,0,0.8);transition:all .3s
}
.projects-list .project .project-title:before{transition:all .3s;content:'[';margin-right:10px;color:#005f6b;display:inline-block;transform:translateX(-10px) scale(0.8) rotate(-25deg);opacity:0
}
.projects-list .project .project-title:after{transition:all .3s;content:']';display:inline-block;margin-left:10px;color:#005f6b;opacity:0;transform:translateX(10px) scale(0.8) rotate(25deg)
}
.projects-list .project .project-title:hover:before{opacity:1;transform:translateX(0) scale(1) rotate(0deg)
}
.projects-list .project .project-title:hover:after{opacity:1;transform:translateX(0) scale(1) rotate(0deg)
}
.projects-list .project .project-title:hover{cursor:pointer;color:#00bad1;transform:scale(1.1)
}
.projects-list .project:hover{box-shadow:0 2px 4px 0 rgba(0,0,0,0.1),0 25px 50px 0 rgba(0,0,0,0.1),inset 0 0 200px 80px rgba(0,0,0,0.8);cursor:pointer
}
.projects-list .project:hover>.project-title,.projects-list .project:hover>.project-description,.projects-list .project:hover>.project-categories{opacity:1;visibility:visible;transform:scale(1);filter:blur(0)
}



