
.tabs-container .tab{padding:20px;border:1px solid rgba(0,0,0,0.16);border-bottom-left-radius:5px;border-bottom-right-radius:5px;border-top-right-radius:5px
}
.slide-fade-unscale-enter-active{transition:all 0.3s cubic-bezier(0.28, 0.02, 0.56, 0.99)
}
.slide-fade-unscale-leave-active{transition:all 0s
}
.slide-fade-unscale-enter,.slide-fade-unscale-leave-active{transform:scale(0.95);opacity:0
}



